 .todoFunctional.app-container {
    height: auto;
    //  background-color: aqua;

    h1 {
        margin-bottom: 4rem;
    }

     .app {
        width: 450px;
        min-height: 600px;
        height: fit-content;
        display: flex;
        flex-direction: column;
        align-items: center;

        // background-color: pink;

        .title {
            margin-bottom: 2.5rem;
            font-weight: bold;
            font-size: 1.4rem;

            .todo-icon {
                margin-right: .5rem;
            }
        }

        .add-todo {
            width: 70%;
            height: 45px;
            position: relative;
            display: flex;
            margin-bottom: 1.3rem;

            input {
                flex: 1;
                border-radius: 20px;
                text-indent: 20px;
                border: none;
                box-shadow: 0 2px 40px 0 rgba(0, 0, 0, 0.07);
                outline: none;
            }

            .add {
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 20px;
                background-color: transparent;
                border: none;
                outline: none;
                cursor: pointer;

                .add-icon {
                    color: rgba(98, 108, 255, 0.748);
                    font-size: 2rem;
                    transition: .1s ease-in-out;

                    &:hover {
                        transform: scale(.97);
                    }
                }
            }
        }

        .todo-items {
            width: 80%;
            list-style-type: none;
            display: flex;
            flex-direction: column;
        }

        .todo-item {
            height: 48px;
            padding-left: 20px;
            margin-bottom: .7rem;
            border-radius: 20px;
            box-shadow: 0 2px 40px 0 rgba(0, 0, 0, 0.07);
            display: flex;
            align-items: center;

            .check {
                margin-right: 1rem;
                height: 18px;
                cursor: pointer;

                .completed {
                    color: rgba(98, 108, 255, 0.748);
                }
            }

            .trash {
                display: inline-block;
                flex: 1;
                height: 18px;
                margin-right: .5rem;
                margin-left: auto;
                overflow: hidden;
                position: relative;
                cursor: pointer;

                &:hover .trash-icon {
                    transform: translateX(0);
                    opacity: 1;
                }
            }

            .trash-icon {
                position: absolute;
                right: 0;
                color: rgb(172, 176, 238);
                background-color: transparent;
                transform: translateX(110%);
                opacity: 0;
                transition: .2s ease-in-out;
            }
        }
     }
 }



